import { useEffect } from "react";
import { useState } from "react";
import { LoadingSpinnerCenter } from "../../../components/utilities/Utilities";
import { useFavorite } from "../../favorite/FavContext";
import { useDb } from "../../home/database/DbContext";
import PostTitle from "../../post/PostTitle";
import { getDatabase, ref, set } from "firebase/database";
import app from "../../account/profile/contextconfig/Config";
import { Container } from "../../../components/layout/Layout";

function Catagory(props) {
  const { title } = useDb();
  const { favorite, addFavorite, removeFavorite } = useFavorite();
  const db = getDatabase();

  const post = [];
  const type = props.type;

  if (title) {
    Object.keys(title).filter((key) => {
      if (title[key].type.includes(type)) {
        post.push(title[key]);
      }
    });
  }

  const deleteHandler = async (id) => {
    set(ref(db, "titles/" + id), null).then(() => {
      set(ref(db, "main/" + id), null);
    });
  };

  const addHandler = async (id, count) => {
    addFavorite(id).then(() => {
      set(ref(db, "titles/" + id + "/like"), { like: count + 1 });
    });
  };

  const removeHandler = async (id, count) => {
    removeFavorite(id).then(() => {
      set(ref(db, "titles/" + id + "/like"), { like: count - 1 });
    });
  };

  return (
    <div>
      {title ? (
        post.length ? (
          <Container className="mt-2 mt-lg-3">
            {post.map((item) => (
              <PostTitle
                key={item.id}
                post={item}
                onDelete={() => deleteHandler(item.id)}
                alreadyFav={favorite.includes(item.id)}
                onAdd={() => addHandler(item.id, item.like.like)}
                onRemove={() => removeHandler(item.id, item.like.like)}
              />
            ))}
          </Container>
        ) : (
          <h2 className="text-center opacity-2 m-4">No {type} Post</h2>
        )
      ) : (
        <LoadingSpinnerCenter className="" />
      )}
    </div>
  );
}

export default Catagory;
