import { useEffect, useState } from "react";
import app from "../../account/profile/contextconfig/Config";
import { getDatabase, set, ref, onValue } from "firebase/database";
import { useHistory, useLocation } from "react-router";
import { Burmese } from "../../../components/burmese converter/BurmeseConverter";
import { Input, InputArea, InputSubmit } from "../../../components/form/Form";
import { FullScreenArrowGoBack } from "../../../components/layout/MyLayout";
import { ToastAlert } from "../../../components/utilities/Utilities";
import { useAuth } from "../../account/profile/contextconfig/AuthContext";

function Edit() {
  const query = new URLSearchParams(useLocation().search);
  const postId = query.get("name");
  const [article, setArticle] = useState();
  const [spinner, setSpinner] = useState(false);
  const [feedback, setFeedback] = useState("");
  const [warn, setWarn] = useState(false);
  const { currentUser } = useAuth();
  const history = useHistory();
  const db = getDatabase();

  useEffect(() => {
    let mount = true;
    const fetchData = async () => {
      onValue(ref(db, "main/" + postId), (snapshot) => {
        const da = snapshot.val();
        if (mount) {
          setArticle(da);
        }
      });
    };
    fetchData();
    return () => (mount = false);
  }, []);

  async function submitHandler(e) {
    e.preventDefault();
    setSpinner(true);
    setFeedback("");

    try {
      set(ref(db, "main/" + postId), article);
      set(ref(db, "titles/" + postId + "/mp3"), { mp3: true });
    } catch (error) {
      setSpinner(false);
      setFeedback("An error occurred. Please try again.");
      return console.log(error.code);
    }

    history.goBack();
  }

  const handleChange = (e) => {
    setWarn(true);
    const { name, value } = e.target;
    setArticle({ ...article, [name]: value });
  };

  return (
    <FullScreenArrowGoBack prompt={warn} mode={true} zu={true}>
      <form className="" onSubmit={submitHandler}>
        <Input
          label={<Burmese str="ေခါင္းစဥ္" />}
          name="title"
          value={article && article.title}
          onChange={handleChange}
        />
        <InputArea
          rows="10"
          label={<Burmese str="ဇာတ္လမ္း" />}
          name="content"
          value={article && article.content}
          onChange={handleChange}
        />
        <Input
          label={<Burmese str="မူရင္း ေရးသားသူ" />}
          name="originalWriter"
          value={article && article.originalWriter}
          onChange={handleChange}
        />

        {currentUser && currentUser.email === "whitedark@gmail.com" && (
          <Input
            req={false}
            label="mp3"
            name="mp3"
            value={article && article.mp3}
            onChange={handleChange}
          />
        )}

        <InputSubmit value="Upload" spinner={spinner} spinning={"Uploading"} />
      </form>
      {feedback && <ToastAlert str={feedback} />}
    </FullScreenArrowGoBack>
  );
}

export default Edit;
