import { getDatabase, onValue, ref } from "@firebase/database";
import app from "../../account/profile/contextconfig/Config";
import { useEffect, useState } from "react";
import { createContext, useContext } from "react";

const dbContext = createContext();

export function useDb() {
  return useContext(dbContext);
}

export function DbContextProvider(props) {
  const [title, setTitle] = useState();
  const [loading, setLoading] = useState(true);
  const db = getDatabase();

  useEffect(() => {
    let mount = true;

    const fetchTitle = async () => {
      onValue(ref(db, "titles"), (snapshot) => {
        const da = snapshot.val();
        if (mount) {
          setTitle(da);
        }
      });
    };

    fetchTitle();
    setLoading(false);
    return () => (mount = false);
  }, []);

  const value = {
    title,
    loading,
  };

  return (
    <dbContext.Provider value={value}>{props.children}</dbContext.Provider>
  );
}
