import { Burmese } from "../../components/burmese converter/BurmeseConverter";
import { InputCheck } from "../../components/form/Form";
import { icons } from "../../icons";

const width = "30px";

export const Straight = (props) => {
  return (
    <InputCheck
      name="Straight"
      onChange={props.onChange}
      checked={props.checked}
    >
      <div className="me-3">
        Straight (<Burmese str="က်ား + မ" />)
        <img src={icons["straight"]} alt="straight" style={{ width: width }} />
      </div>
    </InputCheck>
  );
};
