export default MenuContents;

const Catagory = (props) => {
  const { style } = useStyle();
  let theme = style.theme === "dark" ? "text-light" : "text-dark";

  return (
    <Link
      to={props.url}
      className={
        "text-decoration-none dim border-end p-xl-2 p-1 ps-2 d-block col-12 " +
        theme
      }
    >
      {props.str}
    </Link>
  );
};
