import { Trash } from "react-bootstrap-icons";
import { Container } from "../../components/layout/Layout";
import { FlexBox } from "../../components/layout/MyLayout";
import {
  Card,
  LoadingSpinnerCenter,
} from "../../components/utilities/Utilities";
import { useDb } from "../home/database/DbContext";
import { useFavorite } from "./FavContext";
import FavoritePost from "./FavoritePost";

function Favorite() {
  const { favorite, removeFavorite } = useFavorite();
  const { title } = useDb();

  return (
    <div className="p-2">
      {title ? (
        favorite.length ? (
          <Container className="mt-2 mt-lg-3">
            {favorite.map((item) => {
              if (title[item]) {
                return (
                  <FavoritePost
                    key={item}
                    post={title[item]}
                    onRemove={() => removeFavorite(item)}
                  />
                );
              } else {
                return (
                  <DeletedPost
                    key={item}
                    onClick={() => removeFavorite(item)}
                    id={item}
                  />
                );
              }
            })}
          </Container>
        ) : (
          <h2 className="text-center opacity-50 m-3">No Favorite Post</h2>
        )
      ) : (
        <LoadingSpinnerCenter className="" />
      )}
    </div>
  );
}
export default Favorite;

const DeletedPost = (props) => {
  return (
    <div
      className="col-lg-6"
      title="remove from favorite"
      style={{ cursor: "pointer" }}
      onClick={props.onClick}
    >
      <Card className="mb-2 p-2">
        <FlexBox className="justify-content-between">
          <h5 className="text-center p-1 text-danger">
            Post ID {props.id} got deleted.
          </h5>
          <div
            className="btn dim d-flex align-items-center h5"
            title="Remove from favorite"
          >
            <Trash className={"text-danger"} />
          </div>
        </FlexBox>
      </Card>
    </div>
  );
};
