import { Route, Switch } from "react-router";
import Profile from "../account/profile/Profile";
import Favorite from "../favorite/Favorite";
import Home from "../home/Home";
import Settings from "../setting/Settings";
import Upload from "../upload/Upload";
import MainNav from "./MainNav";
import Menu from "../menu/Menu";
import FullPost from "../post/PostFull";
import Edit from "../post/edit/Edit";
import Catagory from "../menu/catagory/Catagory";
import Mp3 from "../menu/catagory/Mp3";
import "../../mystyles/style.css";

function Main() {
  return (
    <div>
      <Switch>
        <Route path="/" exact>
          <MainNav />
          <SplitScreen left={<Menu />} right={<Home />} />
        </Route>
        <Route path="/settings">
          <MainNav />
          <Settings />
        </Route>
        <Route path="/favorites">
          <MainNav />
          <SplitScreen left={<Menu />} right={<Favorite />} />
        </Route>
        <Route path="/profile">
          <MainNav />
          <Profile />
        </Route>
        <Route path="/upload">
          <Upload />
        </Route>
        <Route path="/post">
          <FullPost />
        </Route>
        <Route path="/edit">
          <Edit />
        </Route>
        <Route path="/straight">
          <MainNav />
          <SplitScreen left={<Menu />} right={<Catagory type="Straight" />} />
        </Route>
        <Route path="/mp3Available">
          <MainNav />
          <SplitScreen left={<Menu />} right={<Mp3 />} />
        </Route>
        <Route path="/lesbian">
          <MainNav />
          <SplitScreen left={<Menu />} right={<Catagory type="Lesbian" />} />
        </Route>
        <Route path="/gay">
          <MainNav />
          <SplitScreen left={<Menu />} right={<Catagory type="Gay" />} />
        </Route>
        <Route path="/threesomeMale">
          <MainNav />
          <SplitScreen
            left={<Menu />}
            right={<Catagory type="ThreesomeMale" />}
          />
        </Route>
        <Route path="/threesomeFemale">
          <MainNav />
          <SplitScreen
            left={<Menu />}
            right={<Catagory type="ThreesomeFemale" />}
          />
        </Route>
        <Route path="/group">
          <MainNav />
          <SplitScreen left={<Menu />} right={<Catagory type="Group" />} />
        </Route>
        <Route path="/incest">
          <MainNav />
          <SplitScreen left={<Menu />} right={<Catagory type="Incest" />} />
        </Route>
        <Route path="/anal">
          <MainNav />
          <SplitScreen left={<Menu />} right={<Catagory type="Anal" />} />
        </Route>
        <Route path="/toys">
          <MainNav />
          <SplitScreen left={<Menu />} right={<Catagory type="Toys" />} />
        </Route>
        <Route path="/bdsm">
          <MainNav />
          <SplitScreen left={<Menu />} right={<Catagory type="BDSM" />} />
        </Route>
        <Route path="/hotwife">
          <MainNav />
          <SplitScreen left={<Menu />} right={<Catagory type="HotWife" />} />
        </Route>
        <Route path="/cuckold">
          <MainNav />
          <SplitScreen left={<Menu />} right={<Catagory type="Cuckold" />} />
        </Route>
        <Route path="/rape">
          <MainNav />
          <SplitScreen left={<Menu />} right={<Catagory type="Rape" />} />
        </Route>
        <Route path="/transgender">
          <MainNav />
          <SplitScreen
            left={<Menu />}
            right={<Catagory type="TransGender" />}
          />
        </Route>
        <Route path="*">
          <MainNav />
          <SplitScreen left={<Menu />} right={<Home />} />
        </Route>
      </Switch>
    </div>
  );
}

export default Main;

const SplitScreen = (props) => {
  return (
    <div className="position-relative">
      <div
        className="float-start w-25 position-sticky top-0 d-none d-lg-block"
        style={{ zIndex: "9989" }}
      >
        {props.left}
      </div>
      <div className="w-100 w-lg-75 ms-lg-25">{props.right}</div>
    </div>
  );
};
