import { InfoCircle } from "react-bootstrap-icons";
import { Link } from "react-router-dom";
import { Collapse, CollapseControl } from "../../components/layout/Layout";

function About() {
  return (
    <div className="p-2" title="Aboutthis web app">
      <CollapseControl
        className="ms-2 py-0 d-block w-100 text-start"
        id="About"
      >
        About <InfoCircle className="float-end" size={23} />
      </CollapseControl>
      <Collapse id="About" className="mt-2">
        <Details ver="v 0.4.1">
          <Ul>
            <li>Small bug fixed on margin-left</li>
          </Ul>
        </Details>
        <Details ver="v 0.4">
          <Ul>
            <li>Changed Top Navigation Bar Style for smaller screen size</li>
            <li>
              Catagory will always appear in top navigation bar for smaller
              screen, can no longer be hidden
            </li>
            <li>Catagory can be hidden in medium screen</li>
            <li>
              Catagory will always appear in left side for PC or larger screen
            </li>
            <li>Post will be 2 columns in medium screen and above</li>
            <li>
              Upload Button is moved to Top Navigation Bar in larger screen
            </li>
          </Ul>
        </Details>
        <Details ver="v 0.3">
          <Ul>
            <li>Changed data fetching system from server</li>
            <li>
              Before : Loading while Downloading All Titles, then Download All
              Contents, after that, there will be no loading time
            </li>
            <li>
              Now: Loading while Downloading All Titles. Each time, open a
              title, Loading while Downloading only its content. Everytime, open
              a different title, there will be loading
            </li>
            <li>Favorite no longer automatically download all contents</li>
            <li>Changed upload icon from right cornor to left</li>
          </Ul>
        </Details>
        <Details ver="v 0.2">
          <Ul>
            <li>
              Added new catagory <Link to="/mp3">Mp3</Link> for mp3 available
              posts
            </li>
            <li>Removed Download features from mp3 files</li>
          </Ul>
        </Details>
        <Details ver="v 0.1.1">
          <Ul>
            <li>Minor fixed in catagories' width for mobile phone</li>
          </Ul>
        </Details>
        <Details ver="v 0.1">
          <p>
            made this web app with React + Firebase + bootstarp 5, including
            some react libraries - react-router-dom, react-mm-text,
            react-bootstrap-icons and Google Drive, python gtts modules
          </p>
        </Details>
      </Collapse>
    </div>
  );
}
export default About;

const Details = (props) => {
  return (
    <details className="m-1">
      <summary>{props.ver}</summary>
      {props.children}
    </details>
  );
};
const Ul = (props) => {
  return <ul className="mb-0">{props.children}</ul>;
};
