import { ContextProvider } from "./components/context/StyleContext";
import { AuthContextProvider } from "./pages/account/profile/contextconfig/AuthContext";
import { FavContextProvider } from "./pages/favorite/FavContext";
import { DbContextProvider } from "./pages/home/database/DbContext";
import Main from "./pages/main/Main";

function App() {
  return (
    <ContextProvider>
      <FavContextProvider>
        <AuthContextProvider>
          <DbContextProvider>
            <Main />
          </DbContextProvider>
        </AuthContextProvider>
      </FavContextProvider>
    </ContextProvider>
  );
}
export default App;
